0deg);\n    }\n\n    &-fill {\n      fill: $background-color;\n    }\n  }\n\n  &__title {\n    font-family: 'JetBrains Mono', monospace;\n    font-size: 32px;\n    color: $text-color;\n    cursor: default;\n    margin: 5rem 0 10rem 0;\n    \n    @media (max-width: 425px) {\n      transform: translateX(-15%);\n    }\n\n    @media (max-width: 320px) {\n      transform: translateX(-25%);\n    }\n  }\n\n  &__form {\n    &-contents {\n      display: flex;\n      gap: 1rem;\n      margin: 0 0 2rem 0;\n\n      textarea {\n        width: 70%;\n        background: transparent;\n        border: none;\n        border-bottom: 3px solid $lighten-color;\n        font-family: $primary-font;\n        font-size: 27px;\n        color: $lighten-color;\n        resize: none;\n        padding: 0;\n        margin: 0;\n\n        &::placeholder {\n          color: $light-color;\n          font-style: italic;\n        }\n\n        &:focus {\n          outline: none;\n        }\n\n        @media (max-width: 1126px) {\n          font-size: 22px;\n        }\n\n        @media (max-width: 1024px) {\n          font-size: 17px;\n        }\n      }\n\n      @media (max-width: 768px) {\n        flex-direction: column;\n        gap: 2rem;\n        padding: 0 2rem;\n      }\n\n      @media (max-width: 375px) {\n        padding: 1rem;\n      }\n    }\n\n    &-content {\n      display: flex;\n      align-items: center;\n      font-family: $primary-font;\n      font-size: 25px;\n      color: $lighten-color;\n\n      label {\n        margin: 0 .5rem 0 0;\n      }\n\n      input {\n        width: 200px;\n        font-size: inherit;\n        border: none;\n        background: transparent;\n        font-family: $primary-font;\n        color: $lighten-color;\n\n        &::placeholder {\n          color: $light-color;\n          font-style: italic;\n        }\n\n        &[type=\"email\"] {\n          width: 400px;\n        }\n\n        &:focus {\n          outline: none;\n        }\n      }\n\n      span {\n  