items: center;\n    gap: 1rem;\n    padding: 10rem 0 0 0;\n\n    @media (max-width: 768px) {\n      width: auto;\n      grid-template-columns: repeat(2, 1fr);\n      gap: 4rem;\n    }\n    \n    @media (max-width: 425px) {\n      transform: translateX(-25%);\n    }\n\n    a {\n      position: relative;\n      width: 100%;\n      height: 100%;\n      display: grid;\n      place-items: center;\n      overflow: hidden;\n\n      span {\n        svg {\n          width: 75px;\n          fill: $lighten-color;\n\n          @media (max-width: 768px) {\n            width: 50px;\n          }\n        }\n      }\n\n      &:nth-child(3) {\n        &:after {\n          font-size: 20px;\n\n          @media (max-width: 1024px) {\n            font-size: 17px;\n          }\n        }\n        &:hover:after {\n          transform: translate3d(0, 35%, 0);\n        }\n      }\n\n      &:after {\n        content: attr(data-text);\n        position: absolute;\n        top: 0;\n        height: 100%;\n        font-family: $secondary-font;\n        font-size: 40px;\n        color: $lighten-color;\n        transform: translate3d(0, 125%, 0);\n        transition: transform .7s cubic-bezier(.42,.97,.52,1.49);\n\n        @media (max-width: 1024px) {\n          font-size: 30px;\n        }\n      }\n\n      & span {\n        transition: transform .7s cubic-bezier(.42,.97,.52,1.49);\n      }\n\n      &:hover:after {\n        transform: translate3d(0, 12%, 0);\n\n        @media (max-width: 1024px) {\n          transform: translate3d(0, 25%, 0);\n        }\n      }\n\n      &:hover span {\n        transform: translate3d(0, -125%, 0);\n      }\n    }\n  }\n}"],"sourceRoot":""}]);
