: #0f2840;\n$first-color-light: #1b3c5c;\n$first-color: #071b2e;\n$dark-color: #0a1724;\n// fonts\n$primary-font: 'ramona';\n$secondary-font: 'durer';\n\n\n/* DEFAULT */\n* {\n  margin: 0;\n  box-sizing: border-box;\n\n  &:before,\n  &:after {\n    box-sizing: border-box;\n  }\n}\n\nhtml,\nbody {\n  overflow-x: hidden;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\n::-webkit-scrollbar {\n  display: none;\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n  background: $dark-color;\n}\n\nbutton,\ninput,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\nimg,\nvideo,\ncanvas,\nsvg {\n  max-width: 100%;\n}\n\na {\n  text-decoration: none;\n  color: inherit;\n}\n\nul {\n  list-style: none;\n}\n\n","@import \"style\";\n\n.footer {\n  width: 100%;\n  height: 100%;\n  background: linear-gradient($first-color-light, $dark-color);\n\n  &__header {\n    position: relative;\n    width: 100%;\n    height: 300px;\n    display: grid;\n    place-items: center;\n    overflow: hidden;\n    padding: 4rem 0;\n\n    &-logo {\n      position: absolute;\n      top: 50%;\n      left: 0;\n      transform: translate3d(0, -50%, 0);\n      display: flex;\n      animation: moveLeft 60s linear infinite;\n\n      @keyframes moveLeft {\n        0% {\n          transform: translate3d(0, -50%, 0);\n        }\n        100% {\n          transform: translate3d(-50%, -50%, 0);\n        }\n      }\n\n      img {\n        height: 275px;\n        margin: 0 5rem;\n      }\n    }\n\n    @media (max-width: 768px) {\n      height: 200px;\n\n      img {\n        height: 175px;\n        margin: 0 3rem;\n      }\n    }\n\n    @media (max-width: 425px) {\n      height: 175px;\n\n      img {\n        height: 150px;\n        margin: 0 2.5rem;\n      }\n    }\n  }\n\n  &__container {\n    position: relative;\n    width: 100%;\n    height: 750px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 8rem;\n    padding: 8rem 4rem 2rem 4rem;\n\n    @media (max-width: 1024px) {\n      height: 600px;\n    }\n\n    &-left {\n      width: 60%;\n      height: 100%;\n    }\n\n    &-right {\n      width: 40%;\n      height: 100%;\n      display: flex;\n      flex-direction: column;\n      align-items: start;\n      justify-content: space-between;\n    }\n\n    @media (max-width: 768px) {\n      height: 800px;\n      flex-direction: column;\n\n      &-left,\n      &-right {\n        width: 100%;\n      }\n\n      &-left {\n        height: 15%;\n      }\n    }\n\n    @media (max-width: 425px) {\n      padding: 8rem 1rem 2rem 1rem;\n    }\n  }\n\n  &__title {\n    font-family: $primary-font;\n    text-transform: uppercase;\n\n    &-first {\n      font-size: 27px;\n      color: $text-color-light;\n      margin: 0;\n    }\n\n    &-second {\n      font-size: 87px;\n      text-align: center;\n      color: $lighten-color;\n      margin: -.75rem 0 0 0;\n    }\n    \n    &-third {\n      font-size: 100px;\n      text-align: end;\n      margin: -3.5rem 0 0 0;\n    }\n\n    @media (max-width: 1024px) {\n      &-first {\n        font-size: 17px;\n      }\n\n      &-second {\n        font-size: 55px;\n        margin: 0;\n      }\n\n      &-third {\n        font-size: 75px;\n        margin: -1.75rem 0 0 0;\n      }\n    }\n\n    @media (max-width: 425px) {\n      &-first {\n        font-size: 15px;\n      }\n\n      &-second {\n        font-size: 45px;\n        margin: 0;\n      }\n\n      &-third {\n        font-size: 50px;\n        margin: -1.25rem 0 0 0;\n      }\n    }\n  }\n\n  &__contact {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: start;\n    justify-content: center;\n\n    &-name {\n      font-family: $secondary-font;\n      font-size: 50px;\n      color: $text-color-light;\n\n      span {\n        color: $first-color-light;\n      }\n    }\n\n    &-email {\n      font-family: $primary-font;\n      font-size: 27px;\n      color: $text-color-light;\n\n      &Hello {\n        color: $light-color;\n      }\n\n      &Lio {\n        color: $background-color;\n        margin: 0 -.35rem;\n      }\n\n      &Humb {\n        color: $lighten-color;\n        margin: 0 -.5rem 0 0;\n      }\n    }\n\n    &-phone {\n      font-family: $primary-font;\n      font-size: 25px;\n      color: $text-color-light;\n\n      &Zero {\n        color: $first-color-light;\n      }\n\n      &First,\n      &Third,\n      &Fifth {\n        color: $light-color;\n      }\n\n      &Second,\n      &Fourth {\n        color: $background-color;\n      }\n    }\n\n    @media (max-width: 1024px) {\n      &-name {\n        font-size: 32px;\n      }\n\n      &-email {\n        font-size: 17px;\n\n        &Lio {\n          margin: 0 -.25rem;\n        }\n\n        &Humb {\n          margin: 0 -.25rem 0 0;\n        }\n      }\n\n      &-phone {\n        font-size: 22px;\n      }\n    }\n\n    @media (max-width: 768px) {\n      &-name {\n        font-size: 47px;\n      }\n\n      &-email {\n        font-size: 25px;\n\n        &Lio {\n          margin: 0 -.35rem;\n        }\n\n        &Humb {\n          margin: 0 -.35rem 0 0;\n        }\n      }\n\n      &-phone {\n        font-size: 30px;\n      }\n    }\n\n    @media (max-width: 425px) {\n      &-name {\n        font-size: 32px;\n      }\n\n      &-email {\n        font-size: 17px;\n\n        &Lio {\n          margin: 0 -.25rem;\n        }\n\n        &Humb {\n          margin: 0 -.25rem 0 0;\n        }\n      }\n\n      &-phone {\n        font-size: 22px;\n      }\n    }\n  }\n\n  &__links {\n    &-list {\n      padding: 0;\n    }\n\n    &-link {\n      position: relative;\n      display: flex;\n      font-family: $secondary-font;\n      font-size: 17px;\n      color: $lighten-color;\n      text-transform: uppercase;\n      letter-spacing: 1px;\n      overflow: hidden;\n      margin: .5rem 0;\n\n      span {\n        transition: transform .7s cubic-bezier(.42,.97,.52,1.49);\n      }\n\n     